// src/components/Chatbot.jsx
import React, { useState, useEffect } from 'react';
import {
  Box,
  Fab,
  Drawer,
  Paper,
  Typography,
  Button,
  IconButton,
  TextField,
  InputAdornment,
} from '@mui/material';
import { ChatBubble, Send, Close } from '@mui/icons-material';

/**
 * Componente Chatbot simple (rule-based).
 * - Guarda historial en localStorage ("chat-history")
 * - Preguntas rápidas y teclado
 * - Respuestas por small KB (regex)
 *
 * Diseño pensado para integrarse en tu App existente con MUI.
 */

const KB = [
  { pattern: /cu(?:á|a)nto?s?\s+años|edad/i, response: 'Tengo 22 años.' },
  { pattern: /d(o|e)nde\s+vives|vives/i, response: 'Vivo en Lima, Perú.' },
  {
    pattern: /tus?\s+habilidades|habilidades/i,
    response: 'Principalmente desarrollo web (React/Node), ML básico y cloud (AWS/GCP/Azure).',
  },
  {
    pattern: /experienc/i,
    response:
      'He trabajado como practicante en SUNAT y soporte en Food Retail S.A.C; también tengo proyectos personales en GitHub.',
  },
  { pattern: /proyectos?|gitHub/i, response: 'Puedes ver mis proyectos en https://github.com/Papiriqui' },
  { pattern: /contacto|email|correo/i, response: 'Puedes contactarme por email: jhonatan.h.salazar@gmail.com' },
  {
    pattern: /contrat(a|ar)|trabajo|oferta/i,
    response: '¡Genial! Puedes enviarme un correo con la oferta o usar el botón "Contrátame" en la página.',
  },
];

function replyFromKB(text) {
  for (const item of KB) {
    if (item.pattern.test(text)) return item.response;
  }
  if (/nombre|como te llam/i.test(text)) return 'Me llamo Jhonatan (este es un demo de chatbot).';
  return "Lo siento, no entendí exactamente. Prueba: '¿Cuántos años tienes?', '¿Dónde vives?', '¿Tus proyectos?'.";
}

export default function Chatbot() {
  const [open, setOpen] = useState(false);
  const [messages, setMessages] = useState(() => {
    try {
      const raw = localStorage.getItem('chat-history');
      return raw
        ? JSON.parse(raw)
        : [{ from: 'bot', text: 'Hola 👋 — pregúntame sobre mi experiencia, proyectos o contacto.' }];
    } catch {
      return [{ from: 'bot', text: 'Hola 👋 — pregúntame sobre mi experiencia, proyectos o contacto.' }];
    }
  });
  const [input, setInput] = useState('');
  const [typing, setTyping] = useState(false);

  useEffect(() => {
    try {
      localStorage.setItem('chat-history', JSON.stringify(messages));
    } catch {}
  }, [messages]);

  const sendMessage = (text) => {
    if (!text.trim()) return;
    const userMsg = { from: 'user', text: text.trim() };
    setMessages((m) => [...m, userMsg]);
    setInput('');

    setTyping(true);
    setTimeout(() => {
      const botReply = replyFromKB(text);
      setMessages((m) => [...m, userMsg, { from: 'bot', text: botReply }]);
      setTyping(false);
    }, 600);
  };

  const quickAsk = (q) => {
    setOpen(true);
    setTimeout(() => sendMessage(q), 180);
  };

  const clearChat = () => {
    const seed = [{ from: 'bot', text: 'Hola 👋 — pregúntame sobre mi experiencia, proyectos o contacto.' }];
    setMessages(seed);
    try {
      localStorage.setItem('chat-history', JSON.stringify(seed));
    } catch {}
  };

  return (
    <>
      {/* Floating button bottom-left */}
      <Box sx={{ position: 'fixed', left: 16, bottom: 20, zIndex: 1400 }}>
        <Fab color="secondary" onClick={() => setOpen((s) => !s)} aria-label="abrir-chat">
          <ChatBubble /> Preguntame
        </Fab>
      </Box>

      {/* Drawer chat */}
      <Drawer
        anchor="left"
        open={open}
        onClose={() => setOpen(false)}
        PaperProps={{ sx: { width: { xs: '100%', sm: 360 } } }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 2, borderBottom: 1 }}>
          <Typography variant="subtitle1">Asistente</Typography>
          <Box>
            <Button size="small" onClick={clearChat}>
              Limpiar
            </Button>
            <IconButton onClick={() => setOpen(false)} aria-label="cerrar-chat">
              <Close />
            </IconButton>
          </Box>
        </Box>

        <Box sx={{ p: 2, height: 'calc(100% - 120px)', overflowY: 'auto', display: 'flex', flexDirection: 'column', gap: 1 }}>
          {messages.map((m, i) => (
            <Box
              key={i}
              sx={{
                display: 'flex',
                justifyContent: m.from === 'user' ? 'flex-end' : 'flex-start',
              }}
            >
              <Paper
                sx={{
                  p: 1.25,
                  maxWidth: '80%',
                  bgcolor: m.from === 'user' ? 'primary.main' : 'background.paper',
                  color: m.from === 'user' ? 'primary.contrastText' : 'text.primary',
                }}
              >
                <Typography variant="body2">{m.text}</Typography>
              </Paper>
            </Box>
          ))}

          {typing && (
            <Box sx={{ display: 'flex', justifyContent: 'flex-start' }}>
              <Paper sx={{ p: 1.25, maxWidth: '60%' }}>
                <Typography variant="body2">Escribiendo...</Typography>
              </Paper>
            </Box>
          )}

          <Box sx={{ mt: 2 }}>
            <Typography variant="caption">Preguntas rápidas</Typography>
            <Box sx={{ display: 'flex', gap: 1, mt: 1, flexWrap: 'wrap' }}>
              <Button size="small" variant="outlined" onClick={() => quickAsk('¿Cuántos años tienes?')}>
                ¿Cuántos años?
              </Button>
              <Button size="small" variant="outlined" onClick={() => quickAsk('¿Dónde vives?')}>
                ¿Dónde vives?
              </Button>
              <Button size="small" variant="outlined" onClick={() => quickAsk('¿Tus proyectos?')}>
                Proyectos
              </Button>
            </Box>
          </Box>
        </Box>

        <Box sx={{ p: 1.25, borderTop: 1 }}>
          <Box
            component="form"
            onSubmit={(e) => {
              e.preventDefault();
              sendMessage(input);
            }}
            sx={{ display: 'flex', gap: 1 }}
          >
            <TextField
              placeholder="Escribe tu pregunta..."
              value={input}
              onChange={(e) => setInput(e.target.value)}
              size="small"
              fullWidth
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton type="submit" aria-label="enviar">
                      <Send />
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </Box>
        </Box>
      </Drawer>
    </>
  );
}
